.product-card {
  box-sizing: border-box;
  padding-inline: 16px;
  width: 200px;
  background-color: $background-color;
  display: flex;
  flex-direction: column;
  border-radius: $border-radius;
  border: 1px solid #f3f3f3;
  font-family: $font-family;

  &__img {
    margin: 32px 3px 40px;
    height: 134px;
    width: 160px;
    display: block;
  }

  &__name {
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    margin: 0;
    color: #000;
  }

  &__name-code {
    margin-top: 4px;
    line-height: 14px;
    color: $secondary-color;
    font-size: 10px;
    margin-bottom: 0;
  }

  &__rating {
    margin-top: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 16px;
  }

  &__reviews {
    font-weight: 400;
    line-height: 14px;
    color: #060b35;
    font-size: 10px;
  }

  &__price {
    height: 18px;
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-name {
      margin: 0;
      color: $secondary-color;
      font-size: 12px;
    }

    &-value {
      margin: 0;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__button-buy {
    margin-top: 16px;
    margin-bottom: 16px;
    height: 38px;
    color: #fff;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    background-color: $primary-color;
    border-radius: $border-radius;
    border: 1px solid $primary-color;
    line-height: 16px;
    font-size: 14px;

    &:hover {
      color: $primary-color;
      border: 1px solid $primary-color;
      border-radius: $border-radius;
      background-color: #fff;
    }
  }
}
